import PrivilegeEnum from "../enums/PrivilegeEnum";
import productDetailsView from "../views/productDetailsView";
import getComments from "../services/jsonPlaceHolderAPI";
import createRemoveButton from "../components/removeButton";
import returnHome from "../helpers/returnHome";
import displayInfoContainer from "../components/infoContainer";
import returnErrorView from "../helpers/returnErrorView";
import NullProduct from "../entities/NullProduct";
export function productDetailsViewLogic(store, mainContent) {
    var product = getProduct(store);
    viewRendering(product, mainContent);
    getProductComments(); //should bringh comments by ProductId from the API
    buyProductEvents(product, store);
    removeProductEvents(product.id, store);
}
//get product
function getProduct(store) {
    var productID = window.location.href.split("?")[1].split("=")[1];
    var product = store.getProductById(productID);
    if (product instanceof NullProduct) {
        returnErrorView();
    }
    return product;
}
//view rendering
function viewRendering(product, mainContent) {
    mainContent.innerHTML = productDetailsView(product);
}
//get product comments
function getProductComments() {
    var productCommentsContainer = document.getElementById("product-comments-container");
    getComments(productCommentsContainer);
}
//buy button event
function buyProductEvents(product, store) {
    var buyButtons = document.getElementsByName("buy-button");
    buyButtons.forEach(function (buyButton) {
        buyButton.addEventListener("click", function () {
            store.cart.add(product);
            updateProductsQuantityInCart(store);
        });
        if (store.user) {
            if (store.user.privilege == PrivilegeEnum.admin) {
                buyButton.after(createRemoveButton());
            }
        }
    });
}
export function updateProductsQuantityInCart(store) {
    var productQuantity = document.getElementById("cart-quantity");
    productQuantity.innerHTML = " &nbsp " + store.cart.showAll().length;
}
//remove button event
function removeProductEvents(productId, store) {
    var removeButtons = document.getElementsByName("remove-button");
    if (removeButtons) {
        removeButtons.forEach(function (button) {
            button.addEventListener("click", function () {
                displayInfoContainer("¿Está seguro que desea eliminar éste producto?.", true, removeItem);
            });
        });
    }
    //remove item from store and return home
    function removeItem() {
        store.removeProduct(productId);
        returnHome();
        displayInfoContainer("El producto ha sido eliminado correctamente.");
    }
}
