import productItem from "../components/productItem";
import productListView from "../views/productListView";
export function productListViewLogic(store, mainContent) {
    viewRendering(mainContent);
    renderProductItems(store);
}
//view rendering
function viewRendering(mainContent) {
    mainContent.innerHTML = productListView();
}
//take products from store and render
function renderProductItems(store) {
    var productContainer = document.getElementById("product-container");
    store.showCatalog().forEach(function (product) {
        productContainer.innerHTML += productItem(product);
    });
}
