export default function cartView(store) {
    var returnComponent;
    if (store.cart.showAll().length != 0) {
        returnComponent = "\n  <div class=\"cart-content\" id=\"main-content\">\n    <table class=\"cart-content-table\">\n      <thead>\n        <tr>\n          <th>Nombre</th>\n          <th>Precio p/u</th>\n          <th>Cantidad</th>\n          <th>Precio neto</th>\n        </tr>\n      </thead>\n      <tbody id=\"cart-table\">\n        <!-- javascript rendered -->\n      </tbody>\n    </table>\n\n    <button class=\"button-link\" id=\"clean-cart-btn\">\n      Vaciar Carrito\n    </button>\n  </div>\n  ";
    }
    else {
        returnComponent = "\n  <div class=\"cart-content\" id=\"main-content\">\n    <h1>Usted no posee art\u00EDculos en su carrito</h1>\n  </div>\n  ";
    }
    return returnComponent;
}
