import createNewProductButton from "../components/newProductButton";
import PrivilegeEnum from "../enums/PrivilegeEnum";
import userView from "../views/userView";
export function userViewLogic(store, mainContent) {
    viewRendering(mainContent, store);
    var navBarContainer = loadNavBarContainer();
    logOutEvent(store, navBarContainer, mainContent);
    privilegeEvents(store, navBarContainer);
}
//view rendering
function viewRendering(mainContent, store) {
    mainContent.innerHTML = userView(store);
}
//load Navigation Bar
function loadNavBarContainer() {
    return document.getElementById("nav-bar-container");
}
//log out events
function logOutEvent(store, navBarContainer, mainContent) {
    var logOutButton = document.getElementById("log-out");
    var userName = document.querySelectorAll(".js-user");
    if (logOutButton) {
        logOutButton.addEventListener("click", function () {
            store.clearUser();
            viewRendering(mainContent, store);
            userName.forEach(function (element) {
                element.innerHTML = "<i class=\"fas fa-user\"></i> &nbsp Invitado";
            });
            removeNewProductLink(navBarContainer);
        });
    }
}
//privilege events
function privilegeEvents(store, navBarContainer) {
    var privilegeOptions = document.getElementById("privilege");
    if (privilegeOptions) {
        setDefaultOption(privilegeOptions, store);
        privilegeOptions.addEventListener("change", function () {
            setUserPrivilege(privilegeOptions, navBarContainer, store);
            store.saveUser();
        });
    }
}
function setDefaultOption(privilegeOptions, store) {
    if (store.user) {
        if (store.user.privilege == PrivilegeEnum.normal) {
            privilegeOptions.selectedIndex = 0;
        }
        else {
            privilegeOptions.selectedIndex = 1;
        }
    }
}
function setUserPrivilege(privilegeOptions, navBarContainer, store) {
    if (privilegeOptions.value == "ADMIN") {
        var newProductButton = createNewProductButton();
        store.user.privilege = PrivilegeEnum.admin;
        navBarContainer.appendChild(newProductButton);
    }
    else {
        store.user.privilege = PrivilegeEnum.normal;
        removeNewProductLink(navBarContainer);
    }
}
function removeNewProductLink(navBarContainer) {
    var productCreationLink = document.getElementById("product-creation-link");
    if (productCreationLink) {
        navBarContainer.removeChild(productCreationLink);
    }
}
