import cartItem from "../components/cartItem";
import cartView from "../views/cartView";
import { updateProductsQuantityInCart } from "./productDetailsViewLogic";
export function cartViewLogic(store, mainContent) {
    viewRendering(mainContent, store);
    addCartProducts(store, mainContent);
    cleanCartButtonEvent(store, mainContent);
}
//view rendering
function viewRendering(mainContent, store) {
    mainContent.innerHTML = cartView(store);
}
//Add cart products to the cart view table
function addCartProducts(store, mainContent) {
    var cart = store.cart.showAll();
    var cartReduced = arrangeCartElements(cart);
    fillCartViewTable(cartReduced);
}
//take product from cart and arrange by id
function arrangeCartElements(cart) {
    var cartReduced = [];
    cart.forEach(function (item) {
        var itemSearch = cartReduced.filter(function (p) { return p.item.id == item.id; });
        if (itemSearch.length === 0) {
            cartReduced.push({
                item: item,
                amount: 1,
            });
        }
        else {
            itemSearch[0].amount++;
        }
    });
    return cartReduced;
}
//load cart products in cart view table
function fillCartViewTable(cartReduced) {
    var cartTable = document.getElementById("cart-table");
    if (cartReduced.length != 0) {
        cartReduced.forEach(function (item) {
            cartTable.innerHTML += cartItem(item);
        });
    }
}
// Remove products from cart button event
function cleanCartButtonEvent(store, mainContent) {
    var cleanCartBtn = document.getElementById("clean-cart-btn");
    if (cleanCartBtn) {
        cleanCartBtn.addEventListener("click", function () {
            store.cart.clear();
            viewRendering(mainContent, store);
            updateProductsQuantityInCart(store);
        });
    }
}
